import { useCallback, useEffect, useRef } from 'react'

export interface Task {
  id: string
  run: (prevResult: any, id: string) => Promise<any>
  onStart?: (id: string) => void
  onSuccess?: (result: any, id: string) => void
  onError?: (error: any, id: string) => void
  _cancelled?: boolean
}

export function useTaskQueue(interval = 100) {
  const queueRef = useRef<Task[]>([])
  const isRunningRef = useRef(false)
  const lastResultRef = useRef<any>(undefined)

  const runQueue = useCallback(async () => {
    if (isRunningRef.current) return

    isRunningRef.current = true

    while (queueRef.current.length > 0) {
      const task = queueRef.current.shift()
      if (!task) continue

      const { id, run, onStart, onSuccess, onError } = task

      if (task._cancelled) {
        console.log(`[task:${task.id}] skipped (cancelled)`)
        continue
      }

      try {
        console.log(`[task:${id}] start`)
        onStart?.(id)
        const result = await run(lastResultRef.current, id)
        lastResultRef.current = result

        console.log(`[task:${id}] success`, result)
        onSuccess?.(result, id)
      } catch (error) {
        console.error(`[task:${id}] failed`, error)
        onError?.(error, id)
      }

      await new Promise(res => setTimeout(res, interval))
    }

    isRunningRef.current = false
  }, [interval])

  const addTask = (task: Task) => {
    queueRef.current.push(task)
    runQueue()
  }

  const removeTask = (id: string) => {
    const task = queueRef.current.find(t => t.id === id)
    if (task) {
      task._cancelled = true
    }
    queueRef.current = queueRef.current.filter(t => t.id !== id)
  }

  useEffect(() => {
    return () => {
      queueRef.current = []
      isRunningRef.current = false
      lastResultRef.current = undefined
    }
  }, [])

  return { addTask, removeTask }
}
